html, body{
  width: 100%;
  height: 100%;
  padding: 0;
  margin:0;
}


slider{
  display: block;
  width: 100%;
  height: 100%;
  background-color: $branco;
  overflow: hidden;
  position: absolute;;
}


slider > *{
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background-color: $branco;
  animation: slide 12s infinite;
  overflow: hidden;
}


slide:nth-child(1){
  left: 0%;
  animation-delay: -1s;
  background-image: url('../img_site/Head/Imagem1.png');
  background-size: cover;
  background-position: center;
}

slide:nth-child(2){
  animation-delay: 2s;
  background-image: url('../img_site/Head/Imagem2.png');
  background-size: cover;
  background-position: center;
}

slide:nth-child(3){
  left: 0%;
  animation-delay: 5s;
  background-image: url('../img_site/Head/Imagem3.png');
  background-size: cover;
  background-position: center;
}
