$cols: 12;
$gutter: 20px;
$l-device: 1200px;
$m-device: 760px;
$s-device: 480px;

//Box-Sizing

*,
*::after,
*::before {
  box-sizing: border-box;
}

//Mixin de media querie

@mixin device($size) {
  @if $size == l {
    @media screen and (min-width: #{$l-device}) {
      @content;
    }
  } @else if $size == m {
    @media screen and (min-width: #{$m-device}) and  (max-width: #{$l-device} - 1) {
      @content;
    }
  } @else if $size == s {
    @media screen and (max-width: #{$m-device} - 1) {
      @content;
    }
  }
}

// função do grid

@function grid($col, $offset: false) {
  $width: $col / $cols * 100 + "%";
  $result: calc(#{$width} - #{$gutter});
  @if $offset == offset {
    $gutter: $gutter / 2;
    $result: calc(#{$width} + #{$gutter});
  }
  @return $result;
}

//Gera s classes de Grid

@for $i from 1 through $cols {
  .grid-#{$i} {
    width: grid($i);
  }
}

//  Mixin Estrutura do grid

@mixin grid-structure {
  float: left;
  min-height: 1px;
  margin-left: $gutter / 2;
  margin-right: $gutter / 2;
  @include device(s) {
    width: 100%;
    float: none;
    margin-left: 0px;
    margin-right: 0px;
  }
}

// Substitue o grid-1,grid-2 grid-3 grid-4 .... (pega todas os atributos class q tenha a classe //grid-  adicionas atributos
[class^="grid-"] {
  @include grid-structure;
}

//  Gerando Class de offset

@for $i from 1 through $cols {
  .offset-#{$i} {
    margin-left: grid($i, offset);
    @extend %offset;
  }
}

@include device(s) {
  %offset {
    margin-left: 0px;
  }
}

// Mixin  de ClearFix

@mixin clearfix {
  &::after,
  &::before {
    content: '';
    display: table;
    clear: both;
  }
}

@mixin container {
  max-width: $l-device;
  margin: 0px auto;
  position: relative;
  @include device(m) {
    max-width: $m-device;
  }
  @include device(s) {
    max-width: $s-device;
    padding-left: 10px;
    padding-right: 10px;
  }
  @include clearfix;
}

.container {
  @include container;
}

@mixin row {
  position: relative;
  margin-left: -$gutter/2;
  margin-right: -$gutter/2;
  @include device(s) {
    margin-left: 0px;
    margin-right: 0px;
  }
  @include clearfix;
}

.row {
  @include row;
}
